<template>
  <QrcodeReader
    :paused="paused"
    @decode="onDecode"
    @init="onInit">

    <div class="content">{{ content }}</div>

    <LoadingIndicator v-show="loading" />
  </QrcodeReader>
</template>

<script>
import { QrcodeReader } from 'vue-qrcode-reader'
import InitHandler from '@/mixins/InitHandler'

import tickets from '@/../public/tickets.json'

const symbols = {
  info: 'â’¾',
  check : 'âœ“',
  poop : 'ðŸ’©',
  cancel : 'âœ˜',
};


export default {
  name: 'Scanner',
  components: { QrcodeReader },

  mixins: [ InitHandler ],

  data () {
    return {
      content: '... ready',
      paused: false,
    }
  },

  methods: {
    onDecode (content) {
      const ticketId = content;
      const docid = tickets[ticketId];

      if(!docid){
        this.content = `${symbols.cancel} Invalid ticket...`
        this.pause = false
        return;
      }

      this.content = `${symbols.check} Valid ticket...`
      this.pause = true

      this.$http.get(`https://devternity-22e74.firebaseio.com/applications/${docid}/tickets.json`, {
        timeout: 7000,
        // use before callback
        before(request) {
          // abort previous request, if exists
          if (this.previousRequest) {
            this.previousRequest.abort();
          }
          // set previous request on Vue instance
          this.previousRequest = request;
        }
      }).then(response => {
        console.log('success', response)
        const data = response.data.find(function(el) {
          return ticketId == el['id']
        });

        this.content = `${symbols.info} ${data['event']} / ${data['holder']['name']}`
      }, response => {
         console.log('error', response);
         this.content = `${symbols.poop} Error: ${response.error}`
      })
    }
  }
}
</script>

<style scoped>
.content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;

  padding: 0px 20px;
  color: #fff;
  font-weight: bold;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>
